// @flow
import React from "react";
import style from "./Preview.module.css";
import { Stage, Layer, Text, Rect } from "react-konva";
import { UrlImage } from "../UrlImage/UrlImage.js";
import brandon from "./brandon.jpg";
import Konva from "konva";
type Props = {
  name: string,
  title: string,
  location: string,
  reason: string,
  profile: string
};

type State = {
  loaded: boolean
};

export default class Preview extends React.Component<Props, State> {
  state = {
    loaded: false
  };

  stageRef: typeof Stage;

  componentDidMount() {
    window.document.fonts.ready.then(
      function() {
        this.setState({ loaded: true });
      }.bind(this)
    );
  }

  handleDownload = () => {
    const dataUrl = this.stageRef.getStage().toDataURL();
    const fileName = `${this.props.name
      .toLowerCase()
      .trim()
      .replace(" ", "_")}_endorses_brandon.png`;
    this.downloadUri(dataUrl, fileName);
  };

  downloadUri(uri: string, name: string) {
    const link = window.document.createElement("a");
    link.download = name;
    link.href = uri;
    window.document.body.appendChild(link);
    link.click();
    window.document.body.removeChild(link);
  }

  getTitleText = () => {
    const { title, location } = this.props;
    return !title.length
      ? location
      : !!location.length
      ? `${title}, ${location}`
      : title;
  };

  renderPreview = () => {
    const { name, reason, profile } = this.props;
    return (
      <div>
        <div className={style.title}>Preview</div>
        <Stage
          width={400}
          height={400}
          ref={node => {
            this.stageRef = node;
          }}
        >
          <Layer>
            <Rect width={400} height={400} fill="#20a8df" />
          </Layer>

          <Layer>
            <UrlImage
              x={0}
              y={0}
              width={400}
              height={160}
              src={brandon}
              filters={[Konva.Filters.Grayscale]}
            />

            {!!profile && (
              <UrlImage x={20} y={20} width={100} height={100} src={profile} />
            )}
          </Layer>
          <Layer>
            <Text
              x={20}
              y={105}
              width={360}
              fontFamily="'Open Sans'"
              fontSize={12}
              align="right"
              fill="#ffffff"
              padding={3}
              text={this.getTitleText()}
            />

            <Text
              x={20}
              y={175}
              fontFamily="'Barlow Condensed'"
              fontSize={30}
              fontWeight={700}
              width={340}
              fill="#ffffff"
              text={name}
            />
            <Text
              x={20}
              y={205}
              fontFamily="'Barlow Condensed'"
              fontSize={30}
              fontWeight={700}
              width={340}
              fill="#354e98"
              text={`endorses Brandon Thomas`}
            />
            {!!reason && (
              <Text
                x={20}
                y={260}
                width={340}
                fontSize={16}
                fontFamily="'Open Sans'"
                text={`"${reason}"`}
              />
            )}
          </Layer>
        </Stage>
        {this.stageRef && (
          <div className={style.button} onClick={this.handleDownload}>
            Download
          </div>
        )}
      </div>
    );
  };

  renderEmpty() {
    return (
      <div className={style.empty}>
        Enter a name, reason, and photo for why{" "}
        <span className={style.highlight}>you</span> support Brandon to generate
        a sharable image!
      </div>
    );
  }
  render() {
    const { name } = this.props;
    const { loaded } = this.state;

    return (
      <div className={style.component}>
        {loaded && name ? this.renderPreview() : this.renderEmpty()}
      </div>
    );
  }
}
